:root {
  --dark: #160901;
  --grey: #a6a5a4;
  --m-grey: #bfbfbf;
  --light-grey: #d9d9d9;
  --white: #fff;
  --light-brown: #8c7370;
  --brown: #594240;
}

body {
  background-color: var(--m-grey);
  max-width: auto;
  margin: 0;
  padding: 0;
}

.search-form {
  background-color: var(--grey);
  border: 1px solid var(--light-brown);
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px 0;
  display: flex;
}

input {
  padding: 10px 15px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  background-color: var(--light-grey);
  border: 1px solid var(--dark);
  width: 325px;
  height: 275px;
  box-shadow: 2px 2px 10px var(--dark);
  border-radius: 8px;
  display: block;
}

.photo-card:hover, .photo-card:focus {
  transition-duration: 1s;
  transform: scale(1.1);
}

.pictures {
  width: 325px;
  height: 200px;
  border-bottom: 1px solid var(--brown);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 0 5px 10px;
  display: flex;
}

.more-hidden {
  justify-content: center;
  padding: 15px 0;
}

.more-resaults {
  padding: 10px 15px;
}

button {
  border: 1px solid var(--brown);
  background-color: var(--light-brown);
  color: var(--dark);
  border-radius: 10px;
}

button:hover, button:focus {
  background-color: var(--brown);
  color: var(--light-grey);
}

h1 {
  text-align: center;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, .34);
  color: #fff;
  margin: 0;
  font-family: Flavors, cursive;
  font-size: 50px;
}

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.bg {
  height: 100%;
  background: url("background.25b45b96.jpg") center / cover no-repeat fixed;
  animation: parallax 10s infinite alternate;
}

@keyframes parallax {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 100% 100%;
  }
}

.content {
  color: #fff;
  text-align: center;
  font-size: 36px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

